<template>
  <div class="head left">
    <div class="blue"></div>
    <div class="logo left">
      <img src="@/assets/image/logo.png" alt="logo" />
    </div>
    <div class="title left">Chen's Page</div>
    <nav class="navs left">
      <div
        class="nav left"
        v-for="nav in navs"
        :key="nav.id"
        :id="nav.id"
        :class="{active: isActive(nav.id)}"
        @click="goto(nav.id)"
      >{{ $t("nav." + nav.id) }}</div>
    </nav>
    <div class="lang right" @click="changeLang()">{{ this.$i18n.locale }}</div>
  </div>
</template>

<script>
export default {
  name: "Head",
  props: {
    msg: String
  },
  data: function() {
    return {
      navs: [
        { id: "home", isActive: false },
        { id: "blog", isActive: false },
        { id: "resume", isActive: false },
        { id: "contact", isActive: false }
      ]
    };
  },
  methods: {
    isActive: function(id) {
      return "/" + id == this.$route.path;
    },
    goto: function(url) {
      location.href = "/#/" + url;
    },
    changeLang() {
      let locale = this.$i18n.locale;
      if (locale === "cn") {
        this.$setLang("en");
      } else {
        this.$setLang("cn");
      }
    }
  }
};
</script>

